import React from 'react';
import './subNav.css';

class SubNav extends React.Component {
    render() {
        return (
            <div className="m-home-subnav borderBot">
                <li><i className="icon_1"></i>FM</li>
                <li><i className="icon_2"></i></li>
                <li><i className="icon_3"></i></li>
                <li><i className="icon_4"></i></li>
            </div>
        );
    }
}

export default SubNav;



